<template lang="html">
<div>
  <popup :title="'Confirm'" :size="'md'" ref="confirmPopup">
    <h4 class="margin-top-0 margin-bottom-20">
      Are you sure you want to delete this show?
    </h4>

    <div class="margin-bottom-30">
      <switch-group
        v-model="deleteReason"
        :options="options">
      </switch-group>
    </div>
    
    <button class="button button-sm red pull-left"
            type="button"
            @click="$refs.confirmPopup.close('cancel')">Cancel
    </button>

    <button class="button button-sm pull-right"
            type="button"
            @click="$refs.confirmPopup.close('yes')">Yes
    </button>
  </popup>

  <div class="watchlist-card" tabindex="0" v-bind:style="{ 'background-image': 'url('+ imgSrc +')' }">
    <button type="button" class="icon-button red dripicons-trash" @click="confirmDelete(id)"></button>

    <h2>{{ heading }}</h2>
    <h4>{{ data.on ? data.on.title : '' }}</h4>
    <h5>On {{ data.on ? `Season ${data.on.season} Episode ${data.on.episode}` : '' }}</h5>
    
    <behind-count-button
        @click="toggleOpen()"
        :count="data.behindCount"
        :open="open">
    </behind-count-button>

    <slide-out-panel :open="open">
        <tabs>
          <tab-button slot="tab-buttons"
              v-for="season in data.show.seasons"
              :key="season._id"
              :name="tabButtonName(season.number)"
              :active="isTabSelected(season)"
              @click="tabSelect(season)">
          </tab-button>

          <tab-panels-container :active="tabActive" slot="tab-panels-container">
              <tab-panel
                v-for="season in data.show.seasons"
                :active="isTabSelected(season)"
                :number="season.number"
                :key="season._id">
                <panel-row
                    v-for="episode in season.episodes"
                    :key="episode._id"
                    :watching-id="id"
                    :number="episode.number"
                    :title="episode.title"
                    :watched="episode.watched"
                    :released="episode.released"
                    @click="watched(episode)">
                </panel-row>
              </tab-panel>
          </tab-panels-container>
        </tabs>
    </slide-out-panel>
  </div>
</div>
</template>

<script>
import Popup from '../Popup/Popup';
import SlideOutPanel from '../SlideOutPanel/SlideOutPanel';

import Tabs from '../Tabs/Tabs';
import TabButton from '../Tabs/TabButton';
import TabPanel from '../Tabs/TabPanel';
import TabPanelsContainer from '../Tabs/TabPanelsContainer';

import PanelRow from './PanelRow';
import BehindCountButton from '../BehindCountButton/BehindCountButton';
import SwitchGroup from '../FormElements/SwitchGroup/SwitchGroup';
import FrostGlass from '../FrostGlass/FrostGlass';
import CountdownTimer from '../CountdownTimer/CountdownTimer';

export default {
  name: 'WatchlistCard',
  props: {
    heading: String,
    details: String,
    id: String,
    data: Object,
    nextAired: Number,
    behindCount: Number,
    imgSrc: String
  },
  data() {
    return {
      open: false,
      tabActive: 1,
      options: [{ id: 1, label: 'Woke Shit' }, { id: 2, label: 'Boring' }, { id: 3, label: 'Done' }],
      deleteReason: ''
    };
  },
  async created() {
    await this.$store.dispatch('watching/getWatchingOn', this.id);
    const { on } = this.data;
    await this.tabSelect(this.data.show.seasons[on.season - 1]);
  },
  methods: {
    toggleOpen() {
      this.open = !this.open;
    },
    async confirmDelete(id) {
      const result = await this.$refs.confirmPopup.open();
      
      if (result == 'yes') {
        await this.$store.dispatch('watching/deleteWatching', {
          id, deleteReason: this.deleteReason
        });
      }
    },
    tabButtonName(number) {
      return number < 10 ? `S0${number}` : `S${number}`;
    },
    async tabSelect(season) {
      await this.$store.dispatch('watching/getEpisodes', { 
        watchingId: this.id,
        seasonId: season._id
      });
      this.tabActive = season.number;

      this.$forceUpdate();
    },
    isTabSelected({ number }) {
      return this.tabActive == number;
    },
    async watched(episode) {
      await this.$store.dispatch('watching/toggleWatched', {
        watchingId: this.id,
        seasonId: episode.season,
        episodeId: episode._id
      });

      this.$forceUpdate();
    }
  },
  components: {
    Popup,
    SlideOutPanel,
    Tabs,
    TabButton,
    TabPanel,
    TabPanelsContainer,
    PanelRow,
    BehindCountButton,
    SwitchGroup,
    FrostGlass,
    CountdownTimer
  }
};
</script>

<style src="./WatchlistCard.scss" lang="scss"></style>
